/* slide track */
.Frame50 {
  justify-content: flex-start;
  align-items: flex-start;
  gap: 32px;
  display: flex;
  perspective: 100px;
  animation: leftright 40s linear infinite;
}
.Frame50:hover {
  animation-play-state: paused;
}

@keyframes leftright {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(calc(1423px));
  }
  100% {
    transform: translateX(0);
  }
}

