.slidecpn {
  width: 1420px;
  height: 268px;
  /* padding-top: 44px; */
  /* padding-bottom: 44px; */
  padding-left: 16px;
  justify-content: flex-end;
  align-items: center;
  display: inline-flex;
  overflow: hidden;
  position: relative;
  margin: 0;
}
/* slider */
.frame52 {
  position: absolute;
  gap: 32px;
  justify-content: flex-start;
  align-items: flex-start;
  display: inline-flex;
  animation: leftright 10s linear infinite;
  overflow: hidden;
  padding-bottom: 80px;
}
/* slide track */
.feedback {
  padding: 32px;
  background: #1e2747;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 32px;
  display: inline-flex;
  overflow: hidden;
}
.frame52:hover {
  animation-play-state: paused;
  cursor: pointer;
}
/* slide */
.frame8 {
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 24px;
  display: flex;
  position: relative;
}
@keyframes leftright {
  0% {
    transform: translateX(940px);
  }
  50% {
    transform: translateX(calc(-25px));
  }
  100% {
    transform: translateX(940px);
  }
}
